<template>
  <div>
  <Heads :head='headData'></Heads>
  <aside class="article" :style='login_ad_url.ad_a'>
  <div class="center">
  <div class="login">
    <div class="loginTitle" v-if="!msg">
      <template v-for="titleNames in titleName">
        <label :class="{ on: active == titleNames }" @click="selected(titleNames)" >{{ titleNames | titlecn}}</label>
      </template>
    </div>

    <form @submit.prevent="" v-if="!msg">
      <div class="form-item">
        <label class="login-icon login-mobile" v-show="active == 'password'"></label>
        <input class="regInput" v-model="mobile" placeholder="请输入手机号" maxlength="11">
        <span v-show="mobile.length == 11"><i :class="{'el-icon-circle-close color-no':!tip,'el-icon-circle-check color-ok':!!tip}" :title="tip"></i></span>
      </div>

      <div class="form-item btn-reg" v-if="active == 'verify'">
        <div class="form-item pass-input">
          <input class="regInput" v-model="verify" placeholder="动态密码">
          <button :disabled="mobile.length < 11 || codeNo" class="btn-phonecode img" @click='code'>{{send}}</button>
          <span class="no-right" v-if="codeTip.verify"><i class="el-icon-circle-close color-no" :title="codeTip.verify.message"></i></span>
          <span class="no-right" v-if="codeTip.mobile"><i class="el-icon-circle-close color-no" :title="codeTip.mobile.message"></i></span>
        </div>
      </div>

      <div class="form-item" v-if="active === 'password'">
        <label class="login-icon login-pass"></label>
        <input class="regInput" type="password" v-model="password" placeholder="请输入密码" >
        <span v-if="codeTip.mobile"><i class="el-icon-circle-close color-no" :title="codeTip.mobile.message"></i></span>
        <span v-if="codeTip.password"><i class="el-icon-circle-close color-no" :title="codeTip.password.message"></i></span>
        
      </div>
      
      <div class="form-item btn-reg" v-if="active === 'password' && captcha_ok && captcha_link || active === 'verify' && captcha_link && captcha_ok">
        <div class="form-item pass-input">
          <input class="regInput" type="text" v-model="captcha" placeholder="输入验证码">
          <img class="img" :src="captcha_link" @click="captchaClick">
        </div>
      </div>

      <div class="login-auto" v-if="active == 'password'"><el-checkbox v-model="checked">7天自动登录</el-checkbox> <span class="login-auto-r">忘记密码？</span></div>

      <div class="form-item btn-reg">
        <button :disabled="!mobile || mVerify || loginText" @click='login' class="btn-register" type="submit"><label v-if="!loginText">登录</label><label v-if="loginText">正在进入...</label></button>
      </div>

    </form>

    <div v-if="msg" class="error">

    {{msg}} 

    <button @click="logout">注销</button> 

    <button @click="users">查看用户信息</button>

    
    <div v-for="u in userData">{{u}}</div>
    </div>

    
    
  </div>

  

  </div>
  </aside>

  <Foot></Foot>
  </div>

</template>

<script>
/* eslint-disable */
import 'normalize.css'
import Vue from 'vue'
import VueResource from 'vue-resource'
import auths from '../../components/auth'
import Heads from '../../components/header'
import Foot from '../../components/footer'


var pic = require('../../assets/login_b1.jpg') 
import { Checkbox, Icon } from 'element-ui'

Vue.component(Checkbox.name, Checkbox)
Vue.component(Icon.name, Icon)
Vue.use(VueResource)

export default {
  components: {
      Heads,
      Foot
  },
  data () {
    return {
      headData:{
            h:'注册',
            text:'还没注册，现在就去',
            url:'/member/reg'
           },
      passwordloginUrl: 'http://api.lessoald.cn/v1/auth/login',//用户名+密码登录
      verifyloginUrl: 'http://api.lessoald.cn/v1/auth/login/mobile',//手机验证码登录
      loginCode: 'http://api.lessoald.cn/v1/auth/login/code',//获取手机验证码
      loginPrecheck: 'http://api.lessoald.cn/v1/auth/login/mobile/precheck',//验证手机号是否可以通过验证码进行登录
      user: 'http://api.lessoald.cn/v1/users',//获取当前登录用户信息
      captchaUrl:'http://api.lessoald.cn/v1/auth/login/captcha',
      header:{ 
        headers:{ 
          Accept:'application/x.lessocloud.v1+json' 
        }
      },
      mobile: '',
      verify: '',
      password:'',
      captcha:'',
      captcha_link:'',
      captcha_ok: false,
      msg:'',
      tip:'',
      codeTip:'',
      send:'发送动态密码',
      codeNo:true,
      userData:'',
      titleName: ['password', 'verify'],
      active: 'password',
      checked: false,
      loginText: false,
      login_ad_url:{
        ad_a:{
          background: 'url('+pic+') center'
        },
        ad_2:{
          background: 'url("/assets/login_b2.jpg")'
        }
      }
    }
  },
  computed: {
      mVerify(){
        let t = this
        if (t.active === 'verify') {
        return t.verify.length < 6  ? true : false
        }
        if (t.active === 'password') {
        return t.password.length < 1  ? true : false
        }
      },
      login_ad(){
        let t = this
        return true
      }
  },
  filters: {
    titlecn: function (value) {
      if (!value) return ''
      if (value == 'verify') {
        return '手机动态码登录' 
      } else if (value == 'password') {
        return '用户名登录'
      }
    }
  },
  watch: {
    mobile: function(val, oldVal){
      if (val.length == 11) {
        this.precheck()
        //this.captchaClick()
      }
    }
  },
  created: function () {
    if ( auths.hasItem('token') ) {
        this.msg = "已登录，手机号为"+ auths.getItem('mobile')
        return
    }
    //this.captchaClick()
  },
  methods: {
    selected(Name) {
      this.active = Name
    },
    login () {
      let vm = this ,urls ,loginCode
      let active = vm.active
      vm.codeTip = ''
      vm.loginText = true
      if (active === 'verify') {
        urls = vm.verifyloginUrl
        loginCode = { 
          mobile:vm.mobile, 
          verify:vm.verify
        }
      } else if (active === 'password') {
        let pass = vm.password
        urls = vm.passwordloginUrl
        vm.codeTip = ''
        loginCode = { 
            mobile: vm.mobile, 
            password: pass,
            captcha: vm.captcha
          }
      }
      
      this.$http.post( urls , loginCode, vm.header)
      .then((res) => {
        let resMsg = res.body
        vm.loginText = false
        if (resMsg.status === 0) {
          vm.codeTip = resMsg.error
          vm.captcha_link = ''
          vm.captcha_ok = false
          if (resMsg.error.mobile.code === 4058 || 4059) {
            vm.captcha_link = resMsg.extra.captcha
            vm.captcha_ok = true
          }else if (resMsg.error.verify.code === 4056 ) {
            vm.codeTip = resMsg.error
          }else if (resMsg.error.mobile.code === 4052) {
            vm.codeTip = resMsg.error
          }
        } else if (res.body.status === 1) {
          let tokenDate
          if (vm.checked) {
            tokenDate = 604800
          }else{
            tokenDate = 3600
          }

          auths.set('token', 'Bearer '+res.body.data.token, tokenDate)
          auths.set('mobile', vm.mobile, tokenDate)
          if ( auths.hasItem('token') ) {
              vm.msg = "已登录，手机号为"+ auths.getItem('mobile')
          }
        }
        
      }, (res) => {
        console.log(JSON.stringify(res))
        vm.loginText = false
      }); 
    },
    precheck(){
      var vm = this
      this.$http.post( vm.loginPrecheck, { mobile: + vm.mobile }, vm.header ).then(
        (res) => {
          if (res.body.result === true) {
            vm.codeNo = false 
            vm.tip = 'OK'
            return
          } else if (res.body.result === false) {
            vm.codeNo = true 
            vm.tip = '该手机号码未注册'
            return
          } else if (res.body.status === 0) {
            vm.codeNo = true 
            vm.tip = '请输入11位的大陆手机号'
            return
          }
       }, 
       (res) => {
          vm.codeNo = true
          vm.tip = '哎呀，出错了'
       }); 
    },
    captchaClick(){
      let vm = this
      let data = vm.captchaUrl
      if (!!vm.captcha_link) {
        vm.captcha_link = vm.captcha_link + '1'
        return
      }
      this.$http.get( data , vm.header ).then(
        (res) => {
          let data = res.body
          if (data.status === 1) {
            vm.captcha_link = data.data.captcha
            return
          }
       }, 
       (res) => {
          vm.codeNo = true
          vm.tip = '出错了'
       }); 
    },
    code () {
      let vm = this,data
      vm.codeNo = true;
      vm.codeTip = '';
      if (!vm.captcha_ok) {
        data =  { 
          mobile:vm.mobile 
        }
      } else {
        data = { 
          mobile:vm.mobile,
          captcha:vm.captcha
        }
      }

      this.$http.post( this.loginCode, data, this.header)
      .then((res) => {
          vm.captcha_ok = false
          let resMsg = res.body
          if (resMsg.status === 1) {
            let t = resMsg.data.interval;
            let setIntervalData =  setInterval(function() {
                  if (t > 0) {
                      vm.codeNo = true;
                      vm.send = '重新发送('+ t-- +')';
                  } else {
                      clearInterval(setIntervalData);
                      vm.codeNo = false;
                      vm.send = '重新发送';
                  }
            }, 1000);
            vm.verify = res.body.data.code
          }else if (resMsg.status === 0) {
            vm.captcha_link = ''
            
            if (resMsg.error.mobile.code === 4060 || 4059) {
              vm.captcha_link = resMsg.extra.captcha
              vm.codeTip = resMsg.error
              vm.codeNo = false;
              vm.captcha_ok = true
            }  
        }

      }, (res) => {
          vm.tip = "用户不存在或帐户被冻结"
      });
    },
    logout () {
      let t = this
      auths.removeItem('token')
      t.msg = t.verify = ''
      t.captcha_link = ''
      t.codeTip = ''
    },
    users() {
      var vm = this
      var token = auths.getItem('token')

      this.$http.get( this.user +"?"+Math.random().toString(36).substring(7), { 
        headers:{ 
          Accept:'application/x.lessocloud.v1+json',
          Authorization:token
        }
      })
      .then((res) => {
        vm.userData = res.body.data
        let token = res.headers.get('Authorization')
        auths.set('token', token, 3600)
      }, (res) => {}); 
    },
    toggleInterval (val) {
        if (val === undefined) { val = this._intervalID }
        if(this._intervalID) {
          clearInterval(this._intervalID)
          delete this._intervalID
        }
        if(val && this.interval > 0) {
          this._intervalID = setInterval(this.next, this.interval)
        }
    }
  },
  ready () {
      this.toggleInterval(true)
  },
  beforeDestroy () {
      this.toggleInterval(false)
  }
}
</script>

<style>
  .article{
    min-width: 1200px;
    width: 100%;
    height: 600px;
    background-color: #f2f2f2;
    display: table;
    content: " ";
  }

  .article .center {
    width: 1000px;
    margin: 0 auto;
  }

  .article .center h3 {
    font-size: 28px;
    font-weight: inherit;
    color: #ff6600;
    margin: 42px auto;
  }
  .login{
    background-color: #fff;
    width: 380px;
    display: inherit;
    float: right;
    margin-top: 100px;
    border-radius: 2px;
    padding-bottom: 20px;
  }
  .login_ad_a{
   /* background: url(../../assets/login_b1.jpg) center;*/
    background: url(../../assets/login_b1.jpg) center;
  }
  .loginTitle{
    display: inline-block;
    height: 60px;
    line-height: 60px;
    margin-bottom: 20px;
    border-bottom: 1px solid #d6dce0;
  }
  .loginTitle input {
    display: none;
  }
  .loginTitle label{
    margin: 0 32px;
    color: #313234;
    font-size: 20px;
  }
  .loginTitle .on{
    color: #f60;
  }

  .form-item{
    position: relative;
    border: solid 1px #ddd;
    width: 300px;
    height: 40px;
    z-index: 0;
    margin: 18px auto;
  }
  .form-item span{
    position: absolute;
    top: 11px;
    right: -28px;
  }
  .form-item .no-right{
    right: -158px;
  }

  .form-item .regInput {
    border: 0 none;
    font-size: 16px;
    width: 200px;
    height: 20px;
    padding: 10px;
    outline: 0;
  }
  .pass-input{
    width: 170px;
    margin: 0;
  }
  .pass-input .img{
    width: 120px;
    height: 42px;
    position: absolute;
    right: -130px;
    border-radius: 2px;
    top: -1px;
  }
  .form-item .pass-input input{
    width: 140px;
  }
  .btn-register {
    width: 100%;
    height: 48px;
    color: #fff;
    background: #ff6600;
    border: 0;
    font-size: 20px;
    outline: 0;
    border-radius: 2px;
  }
  .btn-phonecode {
    border: none;
    background: #ff6600;
    color: #fff;
    padding: 0;
    outline: 0;
  }
  
  .form-item .login-mobile{
    display: inline-block;
    width: 28px;
    height: 21px;
    background-position: 1px 2px;
    margin: 10px;
    float: left;
    border-right: 1px solid #d6dce0;
  }
  .form-item .login-pass{
    display: inline-block;
    width: 28px;
    height: 21px;
    background-position: 1px -32px;
    margin: 10px;
    float: left;
    border-right: 1px solid #d6dce0;
  }
  .btn-reg {
    width: 302px;
    height: 42px;
    border: none;
  }
  .login-auto{
    margin: auto;
    width: 300px;
    color: #313234;
  }
  .login-auto-r{
    float: right;
    font-size: 14px;
  }
  button[disabled], button:disabled {
      background-color: rgba(255,179,128,1);
      color: #fff;
      border-radius: 2px;
  }
  *{
    font-family: "Microsoft YaHei","Hiragino Sans GB";
  }
  .btn-register,.regInput,.btn-phonecode{
    font-family: "Microsoft YaHei","Hiragino Sans GB";
  }
</style>


